import React, {Component} from 'react';
import {Spinner} from 'react-bootstrap';
import '../stylesheet.css';

export default class AsyncImg extends Component {

    constructor(props) {
        super(props);
        this.state = {
            output: <Spinner animation="border" variant="primary" role="status"/>
        };
    }

    componentDidMount() {
        const img = new Image();
        img.onerror = () => {
            this.setState({
                output: <div><h1>Uh Oh! Path not found!</h1></div>
            });
        }
        img.onload = () => {
            this.setState({
                output:
                    <img
                        {...Object.assign({}, this.props, {className: this.props.className + " " + this.props.responsive ? this.props.className + " responsive" : this.props.className, text: undefined})}
                        alt={this.props.alt ? this.props.alt : ""}
                    />
            });
        }
        img.src = this.props.src;
    }

    render() {
        return(
            <div className={this.props.helvetica ? "topMargin" : ""}>
                {this.state.output}
                <p className={this.props.helvetica ? "helvetica" : ""}>{this.props.text}</p>
            </div>
        );
    }
}