// @flow

import React, { Component } from 'react';
import { Container, Row, Col } from 'react-bootstrap';
import {Helmet} from 'react-helmet';
import MediaQuery from 'react-responsive';
import Checkout from './Checkout';
import ReactGA from 'react-ga';

const minDesktopWidth = 1224;
const title = "Donate";
const paragraph = "Pathfinders Robotics is a 501(c)3 nonprofit organization and shall operate exclusively for education and charitable purposes within the meaning of Section 501 (c)(3) of the Internal Revenue Code, or the corresponding section of any future Federal tax code. Pathfinders Robotics’s purpose is to develop and teach youth in grades five through twelve Science, Technology, Engineering, and Math skills (STEM) and to develop teams to compete in FIRST Tech Challenge or other STEM related events in Iowa, Regional, National and, International competitions. Our purpose is to provide volunteer and fund-raising opportunities, per the discretion of the board of directors where the teams present and demonstrate STEM activities with other youth in order to have a greater impact for change. To maximize our impact on current efforts, we may seek to collaborate with other non-profit organizations which fall under the 501(c) (3) section of the internal revenue code and are operated exclusively for educational and charitable purposes.";

export default class Donate extends Component {

  constructor(props) {
    super(props);
    this.state = {
      sponsorshipTiers: null,
    };
    this.setSponsorshipTiers = this.setSponsorshipTiers.bind(this);
  }

  componentDidMount() {
    ReactGA.pageview(window.location.pathname);
  }

  setSponsorshipTiers(tiers) {
    this.setState({sponsorshipTiers: tiers});
  }

  render() {
    return (
      <div id="donate">
        <Helmet>
          <title>Donate | Pathfinders Robotics</title>
          <meta name="description" content="Pathfinders Robotics is a 501(c)3 nonprofit organization and shall operate exclusively for education and charitable purposes within the meaning of Secti..." />
        </Helmet>
        <Container fluid>
          <MediaQuery minDeviceWidth={minDesktopWidth}>
            <Row>
              <Col>
                <h1>{title}</h1>
                <p>{paragraph}</p>
                <br/>
                {this.state.sponsorshipTiers}
              </Col>
              <Col>
                <Checkout setTiers={this.setSponsorshipTiers}/>
                <br/>
              </Col>
            </Row>
          </MediaQuery>
          <MediaQuery maxDeviceWidth={minDesktopWidth}>
            <Row>
            <h1>{title}</h1>
            <p>{paragraph}</p>
            <br/>
            {this.state.sponsorshipTiers}
            <br/>
            </Row>
            <Row>
              <Checkout setTiers={this.setSponsorshipTiers}/>
              <br/>
              <br/>
            </Row>
          </MediaQuery>
        </Container>
      </div>
    );
  }
}
